export default {
	props: {
		size: {
			type: [String, Number],
			default: 90
		},
		src: {
			type: String,
			default: ''
		},
		type: {
			type: String,
			default: 'rounded'
		}
	},
	computed: {
		getStyle () {
			return `width: ${this.size}rpx; height: ${this.size}rpx`
		}
	}
}
